import { styled } from "styled-components";

export const Wrapper = styled.section`
  padding: 40px 0;
  text-align: center;
`;
export const Title = styled.h1`
  margin-bottom: 20px;
  font-weight: 500;
  strong {
    font-weight: 800;
  }
`;
export const Desc = styled.div`
  max-width: 800px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
`;
export const Form = styled.form`
  overflow: hidden;
  display: grid;
  grid-template-rows: auto;
  gap: 0 10px;
  margin-top: 20px;
  align-items: stretch;
`;
export const Input = styled.input`
  font-family: "montserrat";
  display: flex;
  align-items: center;
  padding: 16px 20px;
  font-size: 1rem;
  border: 2px solid #000;
  margin-bottom: 12px;
  &::placeholder {
    font-size: 1.1rem;
    font-weight: 700;
    color: #000;
    opacity: 0.3;
  }
`;
export const Button = styled.button`
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px 20px;
  font-size: 1rem;
  text-transform: uppercase;
  border: 1px solid transparent;
  background-color: #3fd4dd;
  color: #fff;
  font-weight: 700;
  font-family: "montserrat";
  margin-bottom: 12px;
  cursor: pointer;
`;
