import React from "react";
import "./actualite-page-style.scss";
import { Link, graphql, useStaticQuery } from "gatsby";
import HeroSection from "../../molecules/HeroSection/HeroSection";
import { BoxDonation } from "../../molecules/BoxDonation/BoxDonation";
import { AboutSection } from "../../organisms/AboutSection/AboutSection";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import NewsletterSection from "../../organisms/NewsletterSection/NewsletterSection";
import WrapperContainer from "../container/WrapperContainer";
const ActualitePageWrapper = ({ content }) => {
  const { hero, actuaImage } = useStaticQuery(graphql`
    query ActuHeroDetail {
      hero: file(relativePath: { eq: "nos-actualites/hero-image.jpg" }) {
        childImageSharp {
          fluid(quality: 100, maxWidth: 1920) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      actuaImage: file(relativePath: { eq: "actualites/victime-du-mariage-forcé.jpg" }) {
        childImageSharp {
          fluid(quality: 100, maxWidth: 1920) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  `);

  const { mission } = content;
  return (
    <WrapperContainer>
      <HeroSection image={hero.childImageSharp.fluid}>
        <div className="heroInfo d-center">
          <h1 className="heading-standard hero-title-center text-white">{mission.title}</h1>
        </div>
      </HeroSection>
      <div className="container px-large">
        <p className="p-vertical">
          L’actualité de LADIE’S JOY se compose de diverses publications réalisées pour mettre en avant ses actions, ses sorties représentant des temps forts pour l’association mais aussi d’actions
          destinées au grand public – forums, web-conférences, expositions, documentaires…, organisées tout au long de l’année et qui permettent de présenter son mandat et ses activités et de
          témoigner des situations observées sur ses terrains d’intervention. L’actualité de LADIE’S JOY se compose de diverses publications réalisées pour mettre en avant ses actions, ses sorties
          représentant des temps forts pour l’association mais aussi d’actions destinées au grand public – forums, web-conférences, expositions, documentaires…, organisées tout au long de l’année et
          qui permettent de présenter son mandat et ses activités et de témoigner des situations observées sur ses terrains d’intervention. L’actualité de LADIE’S JOY se compose de diverses
          publications réalisées pour mettre en avant ses actions, ses sorties représentant des temps forts pour l’association mais aussi d’actions destinées au grand public – forums, web-conférences,
          expositions, documentaires…, organisées tout au long de l’année et qui permettent de présenter son mandat et ses activités et de témoigner des situations observées sur ses terrains
          d’intervention. L’actualité de LADIE’S JOY se compose de diverses publications réalisées pour mettre en avant ses actions, ses sorties représentant des temps forts pour l’association mais
          aussi d’actions destinées au grand public – forums, web-conférences, expositions, documentaires…, organisées tout au long de l’année et qui permettent de présenter son mandat et ses
          activités et de témoigner des situations observées sur ses terrains d’intervention. L’actualité de LADIE’S JOY se compose de diverses publications réalisées pour mettre en avant ses actions,
          ses sorties représentant des temps forts pour l’association mais aussi d’actions destinées au grand public – forums, web-conférences, expositions, documentaires…, organisées tout au long de
          l’année et qui permettent de présenter son mandat et ses activités et de témoigner des situations observées sur ses terrains d’intervention.
        </p>
        <HeroSection image={actuaImage.childImageSharp.fluid} />

        <p className="p-vertical">
          L’actualité de LADIE’S JOY se compose de diverses publications réalisées pour mettre en avant ses actions, ses sorties représentant des temps forts pour l’association mais aussi d’actions
          destinées au grand public – forums, web-conférences, expositions, documentaires…, organisées tout au long de l’année et qui permettent de présenter son mandat et ses activités et de
          témoigner des situations observées sur ses terrains d’intervention. L’actualité de LADIE’S JOY se compose de diverses publications réalisées pour mettre en avant ses actions, ses sorties
          représentant des temps forts pour l’association mais aussi d’actions destinées au grand public – forums, web-conférences, expositions, documentaires…, organisées tout au long de l’année et
          qui permettent de présenter son mandat et ses activités et de témoigner des situations observées sur ses terrains d’intervention.
        </p>
        <Link className="link-primary" to="/nos-actualites">
          lire d'autres actualités
        </Link>
      </div>
      <NewsletterSection />
    </WrapperContainer>
  );
};
export default ActualitePageWrapper;
