import React from "react";
import Layout from "../../layout/Layout";
import ActualitePageWrapper from "../wrapper/actualite-wrapper/ActualitePageWrapper";

const ActualiteTemplate = ({ pageContext }) => {
  return (
    <Layout>
      <ActualitePageWrapper content={pageContext} />
    </Layout>
  );
};

export default ActualiteTemplate;

export const Head = () => <title>Nos -actualités </title>;
