import React from "react";
import "./NewsletterSection.scss";
import { Wrapper, Title, Desc, Input, Button, Form } from "./NewsletterSectionStyle";
const NewsletterSection = () => {
  return (
    <Wrapper className="NewsletterSection">
      <div className="container">
        <div className="inner">
          <Title className="newsletter-title">
            RESTEZ INFORMÉ(E) DE NOS <strong>ACTIONS</strong>
          </Title>
          <Desc className="desc">
            Vos coordonnées sont utilisées pour vous envoyer nos actualités et faire appel à votre générosité. Vous pouvez à tout moment utiliser le lien de désabonnement intégré dans la newsletter.
          </Desc>
          <Form action="/" className="newsletter-form">
            <Input placeholder="PRENOM*" />
            <Input placeholder="EMAIL*" />

            <Button typeOf="submit">S'inscrire</Button>
          </Form>
        </div>
      </div>
    </Wrapper>
  );
};

export default NewsletterSection;
